.contactUs{
    display:flex;
    flex-direction: column;
    margin:80px;
}
.contactUs__header{
    align-self: center;
    margin-bottom: 20px;
}
.contactUs__header > h1{
    text-align: center;
}
.contactUs__content{
    display:flex;
    justify-content: space-between;
}
.contactUs__content__address{
    display:flex;
    align-items: center;
    
}

.contactUs__content__address__icon{
    width:20% !important;
    
    border-radius: 50% !important;
    height: 50% !important;
}
.contactUs__content__phone{
    display:flex;
    align-items: center;
}
.contactUs__content__phone__icon{
    width:20% !important;
    
    border-radius: 50% !important;
    height: 50% !important;
}
.contactUs__content__email{
    display:flex;
    align-items: center;
}
.contactUs__content__email__icon{
    width:20% !important;
    
    border-radius: 50% !important;
    height: 50% !important;
}

@media (max-width: 800px) { 
    .contactUs__content{
        flex-direction: column;

   }
}